import React from "react";
import PropTypes from "prop-types";

const Bodytext = props => {
  const { html, theme } = props;

  return (
    <React.Fragment>
      <div className="bodytext" dangerouslySetInnerHTML={{ __html: html }} />

      <style jsx>{`
        .bodytext {
          animation-name: bodytextEntry;
          animation-duration: ${theme.time.duration.long};

          :global(h1),
          :global(h2),
          :global(h3),
          :global(h4),
          :global(h5),
          :global(h6) {
            margin: 1.5em 0 1em;
            line-height: ${theme.font.lineHeight.s};
          }
          :global(h1) {
            font-size: ${theme.font.size.xl};
          }
          :global(h2) {
            font-size: ${theme.font.size.l};
            padding-bottom: .5em;
            border-bottom: 1px solid ${theme.color.neutral.gray.c};
          }
          :global(h3) {
            font-size: ${theme.font.size.m};
          }
          :global(h4) {
            font-size: ${theme.font.size.s};
          }
          :global(h5) {
            font-size: ${theme.font.size.xs};
          }
          :global(h6) {
            font-size: ${theme.font.size.xxs};
          }
          :global(p) {
            font-size: ${theme.font.size.s};
            line-height: ${theme.font.lineHeight.xxl};
            margin: 0 0 1em;
          }
          :global(ul) {
            list-style: disc;
            margin: 0 0 1.5em;
            padding: 0 0 0 1.5em;
          }
          :global(ul ul) {
            list-style: circle;
            margin: 0;
            padding: 0 0 0 1.5em;
          }
          :global(ul ul ul) {
            list-style: square;
            margin: 0;
            padding: 0 0 0 1.5em;
          }
          :global(li) {
            margin: 0.7em 0;
            line-height: 1.5;
          }
          :global(ol) {
            margin: 0 0 1.5em;
            padding: 0 0 0 1.5em;
          }
          :global(a) {
            font-weight: ${theme.font.weight.bold};
            color: ${theme.color.brand.primary};
            text-decoration: underline;
          }
          :global(hr) {
            height: .25em;
            padding: 0;
            margin: 1.5em 0;
            background-color: ${theme.color.neutral.gray.c};
            border: 0;
          }
          :global(blockquote) {
            padding: 0 1em;
            margin: 0 0 1em;
            border-left: .4em solid ${theme.color.brand.primaryActive};
            color: ${theme.color.brand.dark};
          }
          :global(table) {
            width: 100%;
            max-width: 100%;
            border-spacing: 0;
            border-collapse: collapse;
            overflow: auto;
            margin: 0 0 1em;
          }
          :global(table th) {
            vertical-align: bottom;
            border-top: 0;
            border-bottom: 2px solid ${theme.color.brand.primaryActive};
            padding: 0.5em;
            line-height: ${theme.font.lineHeight.m};
          }
          :global(table tr:nth-child(odd) td) {
            background-color: #f7fbef;
          }
          :global(table td) {
            vertical-align: top;
            padding: 0.5em;
            line-height: ${theme.font.lineHeight.m};
            border-bottom: 1px solid ${theme.color.brand.lightActive};
          }
          :global(img) {
            max-width: 100%;
          }
          :global(a.gatsby-resp-image-link) {
            border: 0;
            display: block;
            margin: 2.5em 0;
            border-radius: ${theme.size.radius.default};
            overflow: hidden;
            border: 1px solid ${theme.line.color};
          }
          /* => customize syntax highlighting style : prismjs > okaidia */
          :global(:not(pre) > code.language-text) {
            background: ${theme.color.neutral.gray.c};
            text-shadow: none;
            color: #c7254e;
            padding: 0.1em 0.3em 0.2em;
            border-radius: 0.1em;
          }
          :global(pre[class*="language-"]) {
            margin-bottom: 1em;
          }
        }

        @keyframes bodytextEntry {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
      `}</style>
    </React.Fragment>
  );
};

Bodytext.propTypes = {
  html: PropTypes.string.isRequired,
  theme: PropTypes.object.isRequired
};

export default Bodytext;
